/**
 * Bio component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import { useStaticQuery, graphql, Link } from "gatsby"
import styled from "styled-components/macro";
import Image from "gatsby-image"

import twitterLogo from '../assets/images/social-icons/twitter.svg';
import instagramLogo from '../assets/images/social-icons/instagram.svg';
import linkedinLogo from '../assets/images/social-icons/linkedin.svg';
import vkLogo from '../assets/images/social-icons/vk.svg';
import rssLogo from '../assets/images/social-icons/rss.svg';
import notificationsIcon from '../assets/images/social-icons/notifications.svg';

import { rhythm } from "../utils/typography"

const MOBILE_WIDTH = 600;
const MOBILE_QUERY = `(max-width: ${MOBILE_WIDTH}px)`;

const Wrapper = styled.div`
  display: flex;
  margin-bottom: ${rhythm(2.5)};

  @media ${MOBILE_QUERY} {
    flex-direction: column;
  }
`;

const ImageStyled = styled(Image)`
  &&& {
    margin: 0 ${rhythm(0.5)} 0 0;
    border-radius: 50%;

    @media ${MOBILE_QUERY} {
      margin: 0 auto ${rhythm(0.5)}
    }
  }
`;

const messages = {
  en: {
    writtenBy:
      `In this blog Sergey is sharing his experience, thoughts and ideas on many different topics ` +
      `such as software developement, traveling, books, lifestyle, finances, tech and more. ` +
      `Subscribe to social media, RSS or push-notifications to keep in touch!`,
    readInDiffLang: `Этот блог также доступен на русском`
  },
  ru: {
    writtenBy:
      `В этом блоге Сергей делится опытом, мылями и идеями по многим темам, которые ему интересны: ` +
      `разработка ПО, путешествия, книги, лайфстайл, финансы, технологии и многое другое. ` +
      `Подпишись в соц.сетях, RSS или на push-оповещения, чтобы быть на связи!`,
    // writtenBy: `Сергей по профессии - программист. Когда не путешествует, то большую часть времени живет в Санкт-Петербурге, работая удаленно на стартапы со всего мира.`,
    readInDiffLang: `This blog is also available in English`
  }
}

const Bio = ({ langKey = 'en' }: any) => {
  const data = useStaticQuery(graphql`
    query BioQuery {
      avatar: file(absolutePath: { regex: "assets/profile-pic.jpg/" }) {
        childImageSharp {
          fixed(width: 400) {
            ...GatsbyImageSharpFixed
          }
        }
      }
      site {
        siteMetadata {
          author
          social {
            twitter
          }
        }
      }
    }
  `)

  const { author } = data.site.siteMetadata;
  const diffLangHomepage = langKey === 'en' ? '/ru' : '/';

  return (
    <Wrapper>
      <ImageStyled
        fixed={data.avatar.childImageSharp.fixed}
        alt={author}
        style={{
          minWidth: 100,
          minHeight: 100,
          width: 100,
          height: 100,
        }}
        imgStyle={{
          borderRadius: `50%`,
        }}
      />
        <div>
          <p>
            {messages[langKey].writtenBy.replace('%s', author)}
            {` `}<br/>
            <Link to={diffLangHomepage}>
              {messages[langKey].readInDiffLang}
            </Link>.
          </p>

          <div style={{ display: 'flex', alignItems: 'center' }}>
            {
              [
                { logo: twitterLogo, url: 'https://twitter.com/sergey_ulyashev' },
                { logo: instagramLogo, url: 'https://www.instagram.com/sergey.ulyashev' },
                { logo: vkLogo, url: 'https://vk.com/neofox' },
                { logo: linkedinLogo, url: 'https://www.linkedin.com/in/sergey-ulyashev' },
                { logo: rssLogo, url: '/rss.xml' },
                { logo: notificationsIcon, url: '/' },
              ].map(({ logo, url }) =>
                <a
                  href={url}
                  target={"_blank"}
                  style={{ marginRight: 16, cursor: 'pointer', boxShadow: 'none' }}
                >
                  <img src={logo} style={{ height: 36 }} />
                </a>
              )
            }
          </div>
        </div>
    </Wrapper>
  )
}

export default Bio
